import React, { Component } from "react";
import PropTypes from "prop-types";
import { axiosInstanceNoAuth } from "./axiosAPI";
import CardBody from "./material_ui_components/Card/CardHeader.js";
import Paper from '@material-ui/core/Paper';
import image from "./img/genesprDark.png";
import LoadingScreen from "./LoadingScreen";
import Card from "./material_ui_components/Card/Card.js";
import Box from '@material-ui/core/Box'
import Grid from '@material-ui/core/Grid';

const bgStyles = {
  paperContainer: {
      backgroundImage: `url(${image})`,
      
      minHeight: 1000,

     
     


     
  },
  successContainer: {
    width: 700,
    height: 700,
    backgroundColor: '#18db66',

    
},

  cardTitle: {
    textAlign:'center'
  }

};

class AccountActivation extends Component {
  constructor(props) {
    super(props);
    this.state = {
      accountConfirm: null,
    };
  }

  componentDidMount() {
    let token = this.props.match.params.token;
    if (!token) {
      this.setState({ accountConfirm: false });
    } else {
      axiosInstanceNoAuth
        .get(`/user/activate/${token}/$`, {})
        .then((response) => {
          console.log(response);
          this.setState({ accountConfirm: true });
        })
        .catch((error) => {
          console.log(error);
          this.setState({ accountConfirm: false });
        });
    }
  }

  render() {
    let accountConfirm = this.state.accountConfirm;
    return (
      <>
        {accountConfirm === null && <LoadingScreen></LoadingScreen>}
        {accountConfirm === true && (
          <div>
          <Paper
          style={bgStyles.paperContainer}
         >
          <Box
            p={8}
          >

          <Grid  
                alignItems="center"
                justify="center"
                container
                direction="column"
                
                

                > 
          <Card successContainer >
           
                          Konto zostało aktywowane, możesz się zalogować

          </Card>
          </Grid>
          </Box>
           </Paper>
           </div>
        )}
        {accountConfirm === false && (
           <div>
           <Paper
           style={bgStyles.paperContainer}
          >
           <Box
             p={8}
           >
 
           <Grid  
                 alignItems="center"
                 justify="center"
                 container
                 direction="column"
                 
                 
 
                 > 
           <Card >
             <CardBody
                           color="danger"
                          
                         >
                           Błąd, konto nie zostało aktywowane
             </CardBody>
 
 
 
 
           </Card>
           </Grid>
           </Box>
            </Paper>
            </div>
        )}
      </>
    );
  }
}

AccountActivation.propTypes = {};

export default AccountActivation;
